import Vue from 'vue'
import VueRouter from 'vue-router'
import LogIn from '../components/Login.vue'
import CourseHome from "@/components/teacherPages/CourseHome";
import ManageStudent from "@/components/teacherPages/ManageStudent";

Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    redirect: '/coursehome'
  },
  {
    path: '/login',
    name: 'LogIn',
    component: LogIn
  },
  {
    path: '/coursehome',
    name: 'CourseHome',
    component: CourseHome,
    children:[
      { path: '/managestudent', component: ManageStudent },
      { path: '/manageproject', component: () => import('../components/teacherPages/ManageProject')}
    ],
  },
]

const router = new VueRouter({
  routes
})

export default router
